import { FormGroup } from "@angular/forms";
import { MatDialogRef } from "@angular/material/dialog";
import { Alert } from "../modules/alert/common/alert.model";
import { BehaviorSubject, Subject } from "rxjs";

export abstract class DialogComponent<T, TResult> {
  form: FormGroup;
  indicator$ = new BehaviorSubject<boolean>(false);
  alert$ = new Subject<Alert | undefined>();
  dialogRef: MatDialogRef<T> | null = null;

  /**
   * Constructor
   */
  protected constructor(dialog: MatDialogRef<T>, form: FormGroup) {
    this.dialogRef = dialog;
    this.form = form;
  }

  /**
   * Reset
   */
  public reset(): void {
    Object.keys(this.form.controls).forEach((field) => {
      const control = this.form?.get(field);
      control?.setValue(null);
    });
  }

  /**
   * Save dialog
   */
  public submit(): void {
    if (this.form && !this.form.valid) {
      Object.keys(this.form.controls).forEach((field) => {
        const control = this.form?.get(field);
        control?.markAsTouched({ onlySelf: true });
      });
      return;
    }
    this.handleSubmit();
  }

  /**
   * Close dialog
   */
  public close(data: TResult | null = null): void {
    this.dialogRef?.close(data);
  }

  /**
   * Handle submit
   */
  public handleSubmit(): void {
    //
  }
}
