import { Observable, Subject, defer, throwError, BehaviorSubject } from "rxjs";
import { finalize, map, catchError } from "rxjs/operators";
import { HttpErrorResponse } from "@angular/common/http";
import { Alert } from "../modules/alert/common/alert.model";
import { AlertType } from "../modules/alert/common/alert-type.model";

export const prepare =
  <T>(callback: () => void) =>
  (source: Observable<T>): Observable<T> =>
    defer(() => {
      callback();
      return source;
    });

export const indicate =
  <T>(indicator: Subject<boolean> | BehaviorSubject<boolean>) =>
  (source: Observable<T>): Observable<T> =>
    source.pipe(
      prepare(() => indicator.next(true)),
      finalize(() => indicator.next(false))
    );

export const toClass =
  <T>(classType: new () => T) =>
  (source: Observable<T>) =>
    source.pipe(map((val) => Object.assign(new classType(), val)));

export const handleError =
  <T>(
    alert: Subject<Alert | undefined>,
    message: string | undefined = undefined
  ) =>
  (source: Observable<T>): Observable<T> =>
    source.pipe(
      prepare(() => alert.next()),
      catchError((error: ErrorEvent | HttpErrorResponse) => {
        let errorMessage = "";
        if (error.error instanceof ErrorEvent) {
          // client-side error
          errorMessage = error.error.message;
        } else if (error as HttpErrorResponse) {
          // server-side error
          const err = error as HttpErrorResponse;
          errorMessage = err.statusText;
        }
        alert.next({
          message: message ? message : errorMessage,
          type: AlertType.error,
        });
        return throwError(errorMessage);
      })
    );
