import { SelectionModel } from "@angular/cdk/collections";
import { BaseModel } from "../models/base.model";
import { MatTableDataSource } from "@angular/material/table";
import { BehaviorSubject } from "rxjs";

/**
 * Base filter
 */
export interface BaseFilter {}

/**
 * Table component
 */
export abstract class TableComponent<T extends BaseModel> {
  dataSource: MatTableDataSource<T> = new MatTableDataSource<T>([]);
  selection = new SelectionModel<T>(true, []);
  working = false;
  search = false;
  count = 0;
  pageSize = 50;
  pageIndex = 1;
  filterValue = "";
  sortDescending = false;
  sortBy = "Id";
  indicator$ = new BehaviorSubject<boolean>(true);
  expandedElement: T | null = null;
  protected filter: BaseFilter = {};

  /**
   * On search
   * @param value
   */
  onSearch(value: string): void {
    this.filterValue = value;
    this.get();
  }

  onSearchClose(): void {
    this.search = false;
    this.filterValue = "";
    this.get();
  }

  onSort(active: string, direction: string): void {
    this.sortBy = active;
    this.sortDescending = direction !== "asc";
    this.get();
  }

  onPageChange(pageIndex: number, pageSize: number): void {
    this.selection.clear();
    this.pageIndex = pageIndex + 1;
    this.pageSize = pageSize;
    this.get();
  }

  isAllSelected(): boolean {
    return this.selection.selected.length === this.dataSource.data.length;
  }

  masterToggle(): void {
    if (this.isAllSelected()) {
      this.selection.clear();
    } else {
      this.dataSource.data.forEach((row) => this.selection.select(row));
    }
  }

  /**
   * Move item
   * @param item
   * @param currentIndex
   * @returns
   */
  private moveItem(item: T, currentIndex: number): void {
    const prevIndex = this.dataSource.data.findIndex((d) => d === item);
    if (prevIndex === -1) {
      return;
    }
    this.dataSource.data.splice(prevIndex, 1);
    this.dataSource.data.splice(currentIndex, 0, item);
    this.dataSource.filter = "";
  }

  /**
   * Update item
   * @param item
   * @returns
   */
  private updateItem(item: T): void {
    if (item === null || item === undefined) {
      return;
    }
    const i = this.dataSource.data.map((x) => x.id).indexOf(item.id);
    if (i >= 0) {
      this.selection.clear();
      this.dataSource.data.splice(i, 1, item);
      this.dataSource.filter = "";
    }
  }

  /**
   * Update
   * @param result
   * @returns
   */
  update(result: T[] | T): void {
    if (result === undefined) {
      return;
    }
    if (result instanceof Array) {
      result.forEach(this.updateItem);
    } else {
      this.updateItem(result);
    }
  }

  /**
   * Set
   * @param count
   * @param items
   */
  set(count: number, items: T[]): void {
    this.count = count;
    this.dataSource = new MatTableDataSource(items);
  }

  /**
   * Add
   * @param result
   */
  add(result: T[] | T): void {
    if (result instanceof Array) {
      this.dataSource.data.push(...result);
      this.count += result.length;
    } else {
      this.dataSource.data.push(result);
      this.count += 1;
    }
    this.dataSource.filter = "";
  }

  /**
   * Clear all the list
   */
  clear(): void {
    this.selection.clear();
    this.dataSource = new MatTableDataSource<T>([]);
  }

  /**
   * Delete Item(s)
   */
  delete(request: string[] | string): void {
    this.selection.clear();
    if (request instanceof Array) {
      this.dataSource.data = this.dataSource.data.filter(
        (elem) => request.indexOf(elem.id) === -1
      );
      this.count -= request.length;
    } else {
      this.dataSource.data = this.dataSource.data.filter(
        (elem) => request !== elem.id
      );
      this.count -= 1;
    }
  }

  /**
   * Get filter count
   * @returns
   */
  public getFilterCount(): number | null {
    let result = 0;

    Object.values(this.filter).forEach((val) => {
      if (val) {
        result++;
      }
    });

    return result === 0 ? null : result;
  }

  abstract get(): void;
}
